<template>
  <div id="app">
    <router-view :store="$store" @login="onLogin"></router-view>
  </div>
</template>

<script>
export default {
  methods: {
    onLogin (username) {
      this.connect(username)
    }
  }
}
</script>

<style lang="scss">
   @import url('//fonts.googleapis.com/css?family=Comfortaa:300,400,700|Grand+Hotel');
   @import './styles/main.scss'
</style>



