<template>
  <div id="message-scroller" class="message-list">
    <message v-for="message in messages" :data="message" :key="message.createdAt"></message>
  </div>
</template>

<script>
  import Message from './Message'
  import Vue from 'vue'

  export default {
    props: ['messages'],
    components: {
      Message
    },
    watch: {
      messages () {
        Vue.nextTick(() => {
          var elem = document.getElementById('message-scroller')
          elem.scrollTop = elem.scrollHeight
        })
      }
    }
  }
</script>



