<template>
  <form class="chat__messages__form d-f" @submit.prevent="onSubmit">
    <input class="form-control" v-model="message" type="text" placeholder="Ton petit message de princesse...">
    <button class="btn btn--pink btn--lettre" type="submit">Envoi</button>
  </form>
</template>

<script>
  export default {
    data () {
      return {
        message: ''
      }
    },
    methods: {
      onSubmit (e) {
        this.sendMessage(this.message)
        this.message = ''
      }
    }
  }
</script>



