<template>
  <main class="view view--chat container-fluid">
  <div class="chat row ">
    <div class="chat__users col-sm-3">
      <user-list :users="$store.users"></user-list>
    </div><!-- /.col-sm-4 -->
    <div class="chat__messages col-sm-9">
      <message-list :messages="$store.messages"></message-list>
      <message-form></message-form>
    </div><!-- /.col-sm-8 -->
  </div><!-- /.row -->
  </main>
</template>

<script>
  import UserList from '../UserList'
  import MessageList from '../MessageList'
  import MessageForm from '../MessageForm'

  export default {
    props: ['store'], // TODO: Encore utile ?
    components: {
      UserList,
      MessageList,
      MessageForm
    }
  }
</script>



