<template>
    <div class="message-wrapper" :class="{ self: data.self }">
      <span  class="message-author">{{data.username}}</span>
      <p class="chat__messages__message">
      {{data.text}}
    </p>
    <span>{{date}}</span>
  </div>
</template>

<script>
  import moment from 'moment'

  const defaultAvatar = '/static/coeur.png'

  export default {
    data () {
      return {
        _int: null,
        date: null
      }
    },
    props: ['data'],
    computed: {
      avatar () {
        return this.data.avatarUrl ? this.data.avatarUrl : defaultAvatar
      }
    },
    methods: {
      updateDate () {
        this.date = moment(this.data.createdAt, 'MM-DD-YYYY')
      }
    }
  }
</script>



