<template>
<main class="view view--login">
  <div class="login">
    <h1 class="titre-page">Petit Potin</h1>
    <p class="error " v-if="error">The username is invalid</p>
    <form class="login__form" @submit.prevent="onSubmit">
      <input class="login__form__input form-control" v-model="username" type="text" placeholder="Ton petit pseudonyme">
      <button class="btn" type="submit">C'est parti !</button>
    </form>
  </div><!-- /.login -->
</main>
</template>

<script>
  export default {
    data () {
      return {
        error: false,
        username: ''
      }
    },
    methods: {
      onSubmit (e) {
        // Validation
        if (!this.username.match(/^\w{1,15}$/)) {
          this.error = true
        } else {
          this.error = false
          this.$emit('login', this.username)
        }
      }
    },
    created () {
      this.$store.$watch('user', (user) => {
        if (user.id) {
          this.$router.push('/')
        }
      })
    }
  }
</script>



