import io from 'socket.io-client'
import Vue from 'vue'

export const store = new Vue({
  data: {
    user: {},
    messages: [],
    users: []
  }
})

export default {
  install (Vue, options) {
    // Connect socket io
    const socket = io(options.api)

    Vue.prototype.$store = store

    // socket.on('connect', () => {
    //   console.log('connect')
    // })

    // Events
    socket.on('user connected', (user) => {
      store.user = user
    })

    socket.on('new message', (data) => {
      if (!data) return

      console.log(data.author.username)

      store.messages.push({
        username: data.author.username,
        text: data.body,
        self: data.author.id === store.user.id
      })
    })

    socket.on('user joined', ({ clients }) => {
      store.users = clients
    })

    socket.on('getUsers', (clients) => {
      store.users = clients
    })

    socket.on('wizz', (user) => {
      console.log(user + 'wizz')
    })

    Vue.mixin({
      methods: {
        connect (username, avatarUrl) {
          socket.emit('user connected', {
            username,
            avatarUrl
          })
        },
        sendMessage (text) {
          socket.emit('new message', text)
        }
      }
    })
  }
}



